export default {
    data () {
        return {

        }
    },
    props: {
        ticket:{
            type: Object
        }
    },
    computed: {
        ServiceRequestUserLifeCycleStatusCodeText () {
            if (this.ticket.ServiceRequestUserLifeCycleStatusCodeText.indexOf("In Process") != -1) {
                return "In Process"
            } else {
                return this.ticket.ServiceRequestUserLifeCycleStatusCodeText
            }
        }
    }
}



